body {
  font-family: $font-family-sans-serif;
  color: $gray-darker;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  font-weight: $headings-font-weight;
}

.h1, h1 {
  font-size: $font-size-h1;
}

.h2, h2 {
  font-size: $font-size-h2;
}

.h4, h4 {
  font-size: $font-size-h4;
}

